.top-panel{
    display:flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom:4rem;
}
.search-bar{
    display:flex;
    justify-content: flex-start;
    align-items: center;
    padding:1.2rem; 
    width:22.2rem;
    background-color: #F3F5F6;
    border: 1px solid #94A3B4;

border-radius: 40px;

}


.top-panel-heading{
    
font-family: Lato;
font-style: normal;
font-weight: bold;
font-size: 3.6rem;
line-height: 4.3rem;


}

.sort-button{
    font-size:1.6rem;

}
.form-input{ margin-left:1.3rem;
    background-color: #F3F5F6;
    border:0px solid black;
    outline:none;
}