.my-image{
    height:24rem;
    width:24rem;   
}


.profile-container{
    display:flex;
    justify-content:center;

}


.my-card{
    text-align:center;
    
}

.my-card-title{
    font-family: Lato;
font-style: normal;
font-weight: 500;
font-size: 3.4rem;
line-height: 4.1rem;
margin-bottom:1.5rem;

/* identical to box height */


color: #000000;
}

.my-card-sub-title{
    
font-family: Lato;
font-style: normal;
font-weight: 500;
font-size: 1.6rem;
line-height: 1.6rem;
margin-bottom:2rem;
/* identical to box height, or 100% */


/* ontic/text-secondary */

color: #586677;

}

.my-card-content{
    font-family: Lato;
    font-style: normal;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 1.6rem;
    margin-bottom:5.2rem;
    color: #94A3B4;
    

}

.my-card-panel{
    display:flex;
    justify-content: space-between;
    width:30rem;
}
.my-card-panel-button{
    height:7.5rem;
    width:7.5rem;
    border-radius: 50%;
    font-size:3.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition:all 0.3s;

}
.btn-1{
    color:#F2C94C;
    background-color:rgb(242, 201, 76,0.3);
}
.btn-2{
    color:#9B51E0;
    background-color:rgba(155, 81, 224,0.3)
}
.btn-3{
    color:#2D9CDB;
    background-color:rgba(45, 156, 219,0.3);
}

.my-card-panel-button:hover{
    box-shadow:0.3rem 0.3rem 1rem rgba(0,0,0,0.2);
    transform:translateY(-0.3rem);
}