.list-item-container{
    background-color: #F3F5F6;
    height: 20rem;
    display:flex;
    padding:1.8rem;
    margin-bottom:1.2rem;
    position: relative;
    transition:all 0.3s;
}
.list-item-image{
    height:14rem;
    width:14rem;
    object-fit: cover;
}
a{
    cursor:pointer;
}

.list-item-image-container{
    margin-right:2.2rem;
}
.list-item-content{
    padding-top:1rem;
    height:14rem;
    display:flex;
    flex-direction:column;
    align-items: flex-start;
    justify-content: space-between;

}

.list-item-title{
    font-family: Lato;
font-style: normal;
font-weight: 500;
font-size: 2.4rem;
line-height: 2.9rem;
margin-bottom:0.6rem;
}

.list-item-subTitle{
    font-family: Lato;
font-style: normal;
font-weight: 500;
font-size: 1.6rem;
line-height: 1.6rem;
color:#586677;
}


.list-item-footer{
    
font-family: Lato;
font-style: normal;
font-weight: 500;
font-size: 1.6rem;
line-height: 1.6rem;
color:#94A3B4;
}

.more-options{
    position: absolute;
    right:2.4rem;
    bottom:2.5rem;

}



.list-item-container:hover{
    box-shadow:0.3rem 0.3rem 1rem rgba(0,0,0,0.2);
    transform:translateY(-0.3rem);
}
